.main-nav {
  position: relative;
  width: 100%;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .main-nav__toggle {
    background-color: #81b3d2;
  }
  .main-nav--closed .main-nav__toggle::before,
  .main-nav--closed .main-nav__toggle::after {
    content: "";
    position: absolute;
    left: 0;
    width: 22px;
    height: 5px;
    transform: none;
    background-color: #ffffff;
  }
  .main-nav--closed .main-nav__toggle::before {
    top: 4px;
  }
  .main-nav--closed .main-nav__toggle::after {
    top: 13px;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav__toggle {
  width: 22px;
  height: 22px;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 17px;
  left: calc(50% + 117px);
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav__toggle::before {
  left: -3px;
  transform: rotate(45deg);
}
.main-nav__toggle::after {
  left: -3px;
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    background-color: #ffffff;
