  .main-nav__item:not(:first-child):hover {
    border-top: 1px solid #85b1cd;
  }
}
@media (max-width: 767px) {
  .main-nav__item:not(:first-child):active {
    border-top: 1px solid #76abca;
  }
}
@media (max-width: 767px) {
