  flex-direction: column;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .page-footer {
    padding-left: 0;
    padding-right: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 5px;
  }
}
