.feedback__label--rating,
.feedback__label--visited {
  font-size: 18px;
  line-height: 26px;
  align-items: center;
  padding: 5px 0 5px 78px;
  min-height: 56px;
  box-sizing: border-box;
  font-weight: bold;
  text-transform: none;
  cursor: pointer;
}
.feedback__label--rating:hover::before,
.feedback__label--visited:hover::before {
  background-color: #e5e5e5;
}
.feedback__label--rating:active::before,
.feedback__label--visited:active::before {
  background-color: #d5d5d5;
}
.feedback__label--rating:active::after,
.feedback__label--visited:active::after {
  opacity: 0.3;
}
.feedback__label--rating::before,
.feedback__label--visited::before {
  width: 56px;
  height: 56px;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: #f2f2f2;
}
@media (min-width: 1200px) {
  .feedback__label--rating {
    padding-bottom: 0;
    padding-left: 75px;
