}
.feedback__label--rating:not(:last-of-type) {
  margin-bottom: 13px;
}
@media (min-width: 768px) {
  .feedback__label--rating:not(:last-of-type) {
    margin-bottom: 22px;
  }
}
.feedback__label--rating::before {
  border-radius: 50%;
}
.feedback__label--rating::after {
  width: 22px;
  height: 22px;
  content: "";
  position: absolute;
  top: 17px;
  left: 17px;
  display: none;
  border-radius: 50%;
  background-color: #333333;
}
@media (min-width: 768px) {
  .feedback__label--visited {
    width: 46%;
    margin-top: 32px;
  }
}
@media (min-width: 1200px) {
  .feedback__label--visited {
